console.log('ok');


import "./style.css";
import "./index.html"



import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js'
import { GlitchPass } from 'three/examples/jsm/postprocessing/GlitchPass.js'
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js'
import { RGBShiftShader } from 'three/examples/jsm/shaders/RGBShiftShader.js'


var fontLoader = new THREE.FontLoader();
/**
 * Base
 */
// Canvas 1 2 3
const canvas = document.querySelector(".webgl1");
const canvas2 = document.querySelector(".webgl2");
const canvas3 = document.querySelector(".webgl3");

// Group
const group = new THREE.Group();
const group2 = new THREE.Group();
const group3 = new THREE.Group();

// Scene 1 2 3

const scene = new THREE.Scene();
const scene2 = new THREE.Scene();
const scene3 = new THREE.Scene();

/**
 * Sizes
 */
const sizes = {
  width: window.innerWidth / 3,
  height: window.innerHeight,
};

window.addEventListener("resize", () => {
  // Update sizes
  sizes.width = window.innerWidth / 3;
  sizes.height = window.innerHeight;

  // Update camera 1
  camera.aspect = sizes.width / sizes.height;
  camera.updateProjectionMatrix();

  // Update camera 2
  camera2.aspect = sizes.width / sizes.height;
  camera2.updateProjectionMatrix();

  // Update camera 3
  camera3.aspect = sizes.width / sizes.height;
  camera3.updateProjectionMatrix();

  // Update renderer 1
  renderer.setSize(sizes.width, sizes.height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

  // Post Processing



  // Update renderer 2
  renderer2.setSize(sizes.width, sizes.height);
  renderer2.setPixelRatio(Math.min(window.devicePixelRatio, 2));



  // Update renderer 3
  renderer3.setSize(sizes.width, sizes.height);
  renderer3.setPixelRatio(Math.min(window.devicePixelRatio, 2));


});

/**
 * Lights & Camera
 */
// Camera 1
const camera = new THREE.PerspectiveCamera(
  75,
  sizes.width / sizes.height,
  0.1,
  100
);
scene.add(camera);
const controls = new OrbitControls(camera, canvas);
controls.enableDamping = false;
controls.enableZoom = false;
controls.enablePan = false;
camera.position.set(0, 5, 0);
controls.update();
controls.minPolarAngle = Math.PI / 2;
controls.maxPolarAngle = Math.PI / 2;

// Light 1
const directionalLight = new THREE.DirectionalLight(0xd9300b, 1);
directionalLight.position.set(0, 2, 1);
camera.add(directionalLight);

// Camera 2
const camera2 = new THREE.PerspectiveCamera(
  75,
  sizes.width / sizes.height,
  0.1,
  100
);
scene2.add(camera2);
const controls2 = new OrbitControls(camera2, canvas2);
controls2.enableDamping = false;
controls2.enableZoom = false;
controls2.enablePan = false;
camera2.position.set(0, 5, 0);
controls2.update();
controls2.minPolarAngle = Math.PI / 2;
controls2.maxPolarAngle = Math.PI / 2;

// Light 2
const directionalLight2 = new THREE.DirectionalLight(0xd9300b, 1);
directionalLight2.position.set(0, 2, 1);
camera2.add(directionalLight2);

const camera3 = new THREE.PerspectiveCamera(
  75,
  sizes.width / sizes.height,
  0.1,
  100
);
scene3.add(camera3);
const controls3 = new OrbitControls(camera3, canvas3);
controls3.enableDamping = false;
controls3.enableZoom = false;
controls3.enablePan = false;
camera3.position.set(0, 5, 0);
controls3.update();
controls3.minPolarAngle = Math.PI / 2;
controls3.maxPolarAngle = Math.PI / 2;

// Light 2
const directionalLight3 = new THREE.DirectionalLight(0xd9300b, 1);
directionalLight3.position.set(0, 2, 1);
camera3.add(directionalLight3);

/**
 * Objects
 */

// Dodecahedron
const dode = new THREE.Mesh(
  new THREE.DodecahedronGeometry(1.1, 1),
  new THREE.MeshStandardMaterial({ color: 0xd9300b, wireframe: true })
);
dode.position.set(0, 0, 0);
scene.add(dode)
// Torus
const torus = new THREE.Mesh(
  new THREE.TorusGeometry(1, 0.5, 8, 12),
  new THREE.MeshStandardMaterial({ color: 0xd9300b, wireframe: true })
);
torus.scale.set(0.8, 0.8, 0.8);
torus.position.set(0, 0, 0);
scene2.add(torus)
// Knot
const knot = new THREE.Mesh(
  new THREE.TorusKnotGeometry(10, 3, 50, 8),
  new THREE.MeshStandardMaterial({ color: 0xd9300b, wireframe: true })
);
knot.scale.set(0.08, 0.08, 0.08);
knot.position.set(0, 0, 0);
scene3.add(knot)

// Font
var textMaterial = new THREE.MeshBasicMaterial({ color: 0xffffff });

fontLoader.load("fonts/Viga_Regular.json", function (font) {
  // Projects
  var textGeometry = new THREE.TextGeometry("PROJECTS", {
    font: font,
    size: 0.15,
    height: 0.05,
  });
  textGeometry.center();
  var Textmesh = new THREE.Mesh(textGeometry, textMaterial);
  Textmesh.position.set(0, 1.75, 0);
  scene.add(Textmesh)


  // AboutMe
  var textGeometry2 = new THREE.TextGeometry("ABOUT ME", {
    font: font,
    size: 0.15,
    height: 0.05,
  });
  textGeometry2.center();
  var Textmesh2 = new THREE.Mesh(textGeometry2, textMaterial);
  Textmesh2.position.set(0, 1.75, 0);
  scene2.add(Textmesh2)


  // Contact
  var textGeometry3 = new THREE.TextGeometry("CONTACT", {
    font: font,
    size: 0.15,
    height: 0.05,
  });
  textGeometry3.center();
  var Textmesh3 = new THREE.Mesh(textGeometry3, textMaterial);
  Textmesh3.position.set(0, 1.75, 0);
  scene3.add(Textmesh3)
});

/**
 * Renderer 1 2 3
 */
const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
  antialias: true,
  alpha: true,
});
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));


const renderer2 = new THREE.WebGLRenderer({
  canvas: canvas2,
  antialias: true,
  alpha: true,
});
renderer2.setSize(sizes.width, sizes.height);
renderer2.setPixelRatio(Math.min(window.devicePixelRatio, 2));

const renderer3 = new THREE.WebGLRenderer({
  canvas: canvas3,
  antialias: true,
  alpha: true,
});
renderer3.setSize(sizes.width, sizes.height);
renderer3.setPixelRatio(Math.min(window.devicePixelRatio, 2));

/**
 * 
 */
const effectComposer = new EffectComposer(renderer)
effectComposer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
effectComposer.setSize(sizes.width, sizes.height)

const renderPass = new RenderPass(scene,camera)
effectComposer.addPass(renderPass)

const glitchPass = new GlitchPass()
glitchPass.enabled = true
effectComposer.addPass(glitchPass)

const rgbShiftPass = new ShaderPass(RGBShiftShader)
effectComposer.addPass(rgbShiftPass)


const effectComposer2 = new EffectComposer(renderer2)
effectComposer2.setPixelRatio(Math.min(window.devicePixelRatio, 2));
effectComposer2.setSize(sizes.width, sizes.height)

const renderPass2 = new RenderPass(scene2,camera2)
effectComposer2.addPass(renderPass2)

const glitchPass2 = new GlitchPass()
glitchPass.enabled = true
effectComposer2.addPass(glitchPass2)

const rgbShiftPass2 = new ShaderPass(RGBShiftShader)
effectComposer2.addPass(rgbShiftPass2)

const effectComposer3 = new EffectComposer(renderer3)
effectComposer3.setPixelRatio(Math.min(window.devicePixelRatio, 2));
effectComposer3.setSize(sizes.width, sizes.height)

const renderPass3 = new RenderPass(scene3,camera3)
effectComposer3.addPass(renderPass3)

const glitchPass3 = new GlitchPass()
glitchPass.enabled = true
effectComposer3.addPass(glitchPass3)

const rgbShiftPass3 = new ShaderPass(RGBShiftShader)
effectComposer3.addPass(rgbShiftPass3)






/**
 * Animate
 */
const clock = new THREE.Clock();
let lastElapsedTime = 0;

const tick = () => {
  const elapsedTime = clock.getElapsedTime();
  const deltaTime = elapsedTime - lastElapsedTime;
  lastElapsedTime = elapsedTime;

  // Animation

  dode.rotation.y += 0.005;
  torus.rotation.y += 0.005;
  knot.rotation.y += 0.005;

  // Update controls
  controls.update();
  controls2.update();
  controls3.update();

  // Update

  // Renderer
  effectComposer.render();
  effectComposer2.render();
  effectComposer3.render();

  window.requestAnimationFrame(tick);
};

tick();

const activePage = window.location.pathname

const navLinks = document.querySelectorAll('nav a').
forEach(link => {
  if(link.href.includes(`${activePage}`)){
    link.classList.add('active');
  }
})